export default (theme) => ({
    skillsRoot: {
        padding: '2rem',
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        margin: 'auto',
    },
    skillsRootLabel: {
        fontSize: '2rem',
        fontFamily: 'Montserrat',
        [theme.breakpoints.up('md')]: {
            fontSize: '4rem',
        },
    },
    skillsContainer: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start',
        justifyContent: 'flex-start',
        textAlign: 'center',
        [theme.breakpoints.up('md')]: {
            flexWrap: 'nowrap',
        },
    },
    skillsBars: {
        display: 'flex',
        alignItems: 'flex-start',
        justifyContent: 'flex-start',
    },
    skillName: {
        width: '4rem',
        fontSize: '0.8rem',
        fontFamily: 'monospace',
        color: '#ffffff',
        background: '#525d67',
        borderRadius: '0.5rem 0 0 0.5rem',
        padding: '0.4rem',
        textAlign: 'center',
        margin: '0.8rem 0rem',
        [theme.breakpoints.up('md')]: {
            width: '6rem',
            fontSize: '1rem',
            padding: '0.5rem',
            margin: '1rem 0rem',
        },
    },
    skillBarContainer: {
        width: '60vw',
        background: '#cccccc',
        borderRadius: '0 0.5rem 0.5rem 0',
        margin: '0.8rem 0rem',
        [theme.breakpoints.up('md')]: {
            fontSize: '1rem',
            margin: '1rem 0rem',
        },
    },
    skillBarPercent: {
        background: '#7bc1d8', //#6fc3c7
        borderRadius: '0',
        padding: '0.4rem 0',
        textAlign: 'end',
        fontSize: '0.8rem',
        fontFamily: 'monospace',
        [theme.breakpoints.up('md')]: {
            fontSize: '1rem',
            padding: '0.5rem',
        },
    },
})
